"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import Logo from "../components/logo";

export default function DashboardPage() {
  const [user, setUser] = useState(null);
  const [subheading, setSubheading] = useState("");

  // Daily rotating messages
  const messages = [
    "Here’s your day at a glance.",
    "Let’s make today a strong one.",
    "Small steps, big movement.",
    "Fuel smart. Feel better.",
    "Your habits are building momentum.",
    "Every log gets you closer.",
    "Consistency outperforms intensity.",
    "Today is a great day to improve.",
    "Keep showing up. It works.",
    "Precision fuels progress.",
    "Good nutrition starts with awareness.",
    "Little adjustments, big long-term wins.",
    "Fuel choices shape performance.",
    "Eat for the work you want to do.",
    "Recovery starts with fueling right.",
    "Energy in → energy out.",
    "Training smarter starts here.",
    "The best athletes track.",
    "Let’s build something solid today.",
    "Proud of you for checking in.",
    "Your future self appreciates this.",
    "Keep going. You're doing better than you think.",
    "Slow and steady is still forward.",
    "Tracking initialized.",
    "System calibrated for today.",
    "Your data, your advantage.",
    "Fuel metrics updated.",
    "Dashboard ready.",
    "Inputs synced.",
    "Look at you staying consistent.",
    "Your nutrition arc continues.",
    "Hydrate. Then dominate."
  ];

  // Get the subheading for today's date
  useEffect(() => {
    const today = new Date().getDate();
    const idx = today % messages.length;
    setSubheading(messages[idx]);
  }, []);

  // Fetch current user
  useEffect(() => {
    async function fetchUser() {
      try {
        const res = await fetch("/api/auth/me");
        if (res.ok) {
          const data = await res.json();
          setUser(data.user);
        }
      } catch (err) {
        console.error("Error fetching user:", err);
      }
    }

    fetchUser();
  }, []);

  const displayName =
    user?.preferredName ||
    user?.firstName ||
    "there";

  const formattedDate = new Date().toLocaleDateString("en-US", {
    weekday: "long",
    month: "long",
    day: "numeric",
  });

  return (
    <main className="min-h-screen bg-slate-50 text-slate-900 flex flex-col">

      {/* NAVBAR */}
      <nav className="w-full flex items-center justify-between px-8 py-4 bg-white shadow-sm">
        {/* Logo → returns to dashboard */}
        <Link href="/dashboard" className="flex items-center gap-3">
          <Logo />
          <span className="text-2xl font-semibold tracking-tight">
            ZeroFlow
          </span>
        </Link>

        <div className="flex items-center gap-4 text-sm">
          <Link
            href="/food/log"
            className="text-blue-700 hover:underline"
          >
            Log Food
          </Link>

          <Link
            href="/workout/log"
            className="text-blue-700 hover:underline"
          >
            Log Workout
          </Link>

          <Link
            href="/settings"
            className="text-slate-600 hover:underline"
          >
            Settings
          </Link>
        </div>
      </nav>

      {/* DASHBOARD HEADER */}
      <header className="flex flex-col items-center mt-10 px-4 text-center">
        <h1 className="text-3xl font-bold">
          Hello, {displayName}.
        </h1>

        <p className="text-slate-600 mt-2">
          {subheading}
        </p>

        <p className="text-slate-400 text-sm mt-1">
          {formattedDate}
        </p>
      </header>

      {/* MAIN CONTENT */}
      <section className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6 p-8 max-w-6xl mx-auto mt-10">

        {/* Food Logging Card */}
        <Link
          href="/food/log"
          className="bg-white rounded-xl shadow p-6 border border-slate-100 hover:shadow-md transition"
        >
          <h3 className="text-xl font-semibold mb-2">Add Food</h3>
          <p className="text-slate-600">Quickly log meals and track your intake.</p>
        </Link>

        {/* Workout Logging Card */}
        <Link
          href="/workout/log"
          className="bg-white rounded-xl shadow p-6 border border-slate-100 hover:shadow-md transition"
        >
          <h3 className="text-xl font-semibold mb-2">Add Workout</h3>
          <p className="text-slate-600">Track gym sessions, runs, and more.</p>
        </Link>

        {/* Placeholder for Reordering Future */}
        <button
          className="bg-blue-700 text-white rounded-xl shadow p-6 border border-blue-800 hover:bg-blue-800 transition"
        >
          Customize Dashboard
        </button>
      </section>

      {/* FOOTER */}
      <footer className="flex justify-center mt-16 pb-8 text-slate-500 text-sm">
        © ZeroFlow LLC 2025
      </footer>
    </main>
  );
}
